      if (date.getFullYear() === year) {
        return date.getMonth() <= 4;
      } else if (date.getFullYear() + 1 === year) {
        return date.getMonth() >= 5;
      }
    }
    return false;
  }
}

(async () => {
  level.value = await useValidate();
  await refreshEmployeeList();
  const response = await fetch(useApiUrl('/api/config'));
  const data = await response.json();
  maxRtt.value = data.rtt;
})();

async function refreshEmployeeList() {
  const stats_response = await fetch(
    useApiUrl('/employees/list'),
    {
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + sessionStorage.getItem("sessionToken"),
      },
    }
  );
  if (stats_response.status === 200) {
    const json: EmployeeDB[] = await stats_response.json();
    stats.value.employee_count = json.length;
    users.value = [];
    for (const user of json) {
      const newUser = { ...user, rtt: 0, rc: 0, cp: 0 };
      for (const conge of user.conges) {
        switch (conge.type) {
          case "Congés payés":
            if (isLeaveThisYear(conge.type, conge.day)) {
              newUser['cp'] += conge.demi ? 0.5 : 1;
            }
            break;
          case "Repos compensateur":
            if (isLeaveThisYear(conge.type, conge.day)) {
              newUser['rc'] += conge.demi ? 0.5 : 1;
            }
            break;
          case "RTT":
            if (isLeaveThisYear(conge.type, conge.day)) {
              newUser['rtt'] += conge.demi ? 0.5 : 1;
            }
            break;
        }
      }
      users.value.push(newUser);
    }
  }
}

function getCongeType(contract: string) {
  switch (contract) {
    case 'Forfait jour':
      return 'RTT';
    case 'Forfait mixte':
    case 'Forfait standard':
    case 'Forfait ETAM':
      return 'RC';
    default:
      return '';
  }
}

