import { useApiUrl } from "@/hooks/useApiUrl";
import router from "@/router";

export async function useValidate() {
  const validate_response = await fetch(
    useApiUrl('/auth/validate'),
    {
      headers: {
        Authorization: "Bearer " + sessionStorage.getItem("sessionToken"),
      },
    }
  );
  if (validate_response.status != 200) {
    await router.push("/");
    return 0;
  } else {
    const json = await validate_response.json();
    return json.level;
  }
}